import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Snackbar from '@material-ui/core/Snackbar';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Switch from '@material-ui/core/Switch';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { bleDevice } from './ble'
import { PageIsVoiceRecogResult } from './pageVoiceRecogResult';
// import { PageIsVoiceRecogSettings } from './pageVoiceRecogSettings';
import { PageIsIndoorEnvironment } from './pageIndoorEnvironment';
// import { PageIsInstructions } from './pageInstructions';


const drawerWidth         = 200;
const isFinished          = 0;
const isInProgress        = 1;
const maxOfCountdown      = 10;
const isVoiceRecogResult  = "V/R Result";
const isVoiceRecogSettigs = "V/R Settings";
const isIndoorEnvironment = "Indoor Environment";
const isLedControl        = "LED Control";
const isInstructions      = "Instructions";
const isCfThreshold       = "Confidence Threshold";
const isVdThreshold       = "Voice Detection Threshold";
const isGain              = "Gain";
const isNoiseSuppressor   = "Noise Suppressor";
const isBeamForming       = "Beam Forming";

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  title: {
    flexGrow: 1,
    color: 'white',
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#333333',
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));



function App(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const Countdown = useRef(maxOfCountdown);
  const timeOutId = useRef(null);
  const tmpWordInfo = useRef([]);
  const bleConnected = useRef(0);
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [bleButtonClicked, setbleButtonClicked] = React.useState(false);
  const [connectionText, setConnectionText] = React.useState("CONNECT");
  const [snackbarState, setSnackbarState] = React.useState({
    open : true,
    vertical : 'bottom',
    horizontal: 'center',
    message : 'Please click or tap "CONNECT" on the top-right',
    autoHideDuration : 5000,
  });
  const [wordInfo, setWordInfo] = React.useState([]);
  const [sensorData, setSensorData] = React.useState({
    temperature: 0.0,
    humidity: 0.0,
    co2: 0.0,
    tvoc: 0.0,
    iaq: 0.0,
  });
  const [voiceRecogResult, setVoiceRecogResult] = React.useState({
    wordId : 0,
    status : 0,
    score : 0,
  });
  const [currentPage, setCurrentPage] = React.useState(isVoiceRecogResult);
  const [openCircle, setOpenCircle] = React.useState(false);
  const [openNestedList, setOpenNestedList] = React.useState(false);
  const [switchChecked, setSwitchChecked] = React.useState([]);

  const handleSwitchcheckedToggle = (value) => () => {
    const currentIndex = switchChecked.indexOf(value);
    const newChecked = [...switchChecked];

    if (currentIndex === -1) {
      newChecked.push(value);
      bleDevice.writeValueWithoutResponse (bleDevice.bleInfo.device, 
                                           bleDevice.bleInfo.characteristicInfo.ledControl.characteristic, 
                                           [1]);
    } else {
      newChecked.splice(currentIndex, 1);
      bleDevice.writeValueWithoutResponse (bleDevice.bleInfo.device, 
                                           bleDevice.bleInfo.characteristicInfo.ledControl.characteristic, 
                                           [0]);
    }

    setSwitchChecked(newChecked);
  };

  const handleNestedListClicked = () => {
    setOpenNestedList(!openNestedList);
  };

  const handleSnackbarOpen = (text) => {
    setSnackbarState({...snackbarState, open : true, message : text});
  }

  const handleSnackbarClose = () => {
    setSnackbarState({...snackbarState, open: false});
  }

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleBleToggle = () => {
    if (!bleConnected.current) {
      bleConnected.current = 1;
      tmpWordInfo.current = [];
      setbleButtonClicked(true);
      bleDevice.connectDevice(bleDevice.bleInfo.device, bleDevice.options, handleOnDisconnected)
      .then(server => {        
        setOpenCircle(true);
        handleSnackbarOpen("Get the service");
        return bleDevice.getService(server, bleDevice.bleInfo.serviceInfo.uuid);
      })
      .then(service => {
        handleSnackbarOpen("Get the characteristic");
        return bleDevice.getCharacteristics(service);
      })
      .then(characteristics => {
        return bleDevice.setCharacteristic(characteristics);
      })
      .then(_ => {
        handleSnackbarOpen("Get Voice Recognition Information");
        return readVoiceRecognitionInformation();
      })
      .then(_ => {
        handleSnackbarOpen("Start notification");
        return bleDevice.startNotifications(bleDevice.bleInfo.characteristicInfo.voiceRecogResult, 
                                            handleVoiceRecognitionResult);
      })
      .then(_ => {
        return bleDevice.startNotifications(bleDevice.bleInfo.characteristicInfo.envSensor, 
                                            handleEnvironmentalSensor);
      })
      .then(_ => {
        if (timeOutId.current) clearTimeout(timeOutId.current);
        Countdown.current = maxOfCountdown;
        timeOutId.current = null;
        bleConnected.current = 1;
        setOpenCircle(false);
        setbleButtonClicked(false);
        handleSnackbarOpen("Measurement is started");
        setConnectionText("DISCONNECT");
      })
      .catch(error => {
        bleConnected.current = 0;
        handleSnackbarOpen("" + error);
        console.log(error);

        if (bleDevice.bleInfo.device) {
          timeOutId.current = setTimeout(handleBleToggle, 10000);
          handleSnackbarOpen("Reconnection countdown: " + Countdown.current);        
          console.log("Reconnection countdown: " + Countdown.current);
          if (Countdown.current > 0) {
            Countdown.current--;    
          }
          else {
            if (timeOutId.current) clearTimeout(timeOutId.current);
            Countdown.current = maxOfCountdown;
            timeOutId.current = null; 
            setOpenCircle(false);
            setbleButtonClicked(false);
            handleSnackbarOpen("Cannot connect device...");
            console.log("Cannot connect device...");
          } 
        }
        else {
          setConnectionText("CONNECT");
          setbleButtonClicked(false);
        }  
      });
    }
    else {
      setbleButtonClicked(true);
      bleConnected.current = 0;
      setConnectionText("CONNECT");
      bleDevice.disconnect(bleDevice.bleInfo.device);
      setbleButtonClicked(false);
      handleSnackbarOpen("The device is disconnected");
    }
  }

  const handleOnDisconnected = () => {
    if (bleConnected.current) {
      bleConnected.current = 0;
      console.log("unexpected disconnection");
      handleBleToggle();
    }
    else {
      bleConnected.current = 0;
    }
  }

  const handleVoiceRecognitionResult = (event) => {
    let value = event.target.value;
    let result = {}; 
    result.wordId  = value.getUint8(0);
    result.status  = value.getUint8(1);
    result.score   = value.getUint8(2);
    // Log
    console.log("Word ID : " + result.wordId);
    console.log("Result : "  + result.status);
    console.log("Score : "   + result.score);

    setVoiceRecogResult({...result});
  }

  const handleEnvironmentalSensor = (event) => {
   const value = event.target.value;
   const sign = value.getUint8(0);
   const result = {};
   if (sign === 1) {
     result.temperature  = (-1) * (value.getUint8(1) + (value.getUint8(2) * 0.01));
   }
   else {
    result.temperature  = value.getUint8(1) + (value.getUint8(2) * 0.01);
   } 
   result.humidity  = (value.getUint8(3) << 8)  + value.getUint8(4)  + (value.getUint8(5) * 0.01);
   result.co2   = (value.getUint8(6) << 8)  + value.getUint8(7)  + (value.getUint8(8) * 0.01);
   result.tvoc  = (value.getUint8(9) << 8)  + value.getUint8(10) + (value.getUint8(11) * 0.01);
   result.iaq   = (value.getUint8(12) << 8) + value.getUint8(13) + (value.getUint8(14) * 0.01);
    // Log
    console.log("Temperature : " + result.temperature);
    console.log("Humidity : "    + result.humidity);
    console.log("CO2 : "         + result.co2);
    console.log("TVOC : "        + result.tvoc);
    console.log("IAQ : "         + result.iaq);

    setSensorData({...result});
  }   

  const readVoiceRecognitionInformation = () => {
    return bleDevice.readValue(bleDevice.bleInfo.characteristicInfo.voiceRecogInfo.characteristic)
    .then(information => {
      let result = {};
      result.status   = information.getUint8(0);
      result.cf       = information.getUint8(1);
      result.vd       = (information.getUint8(2) << 8) + information.getUint8(3);
      result.gain     = information.getUint8(4);
      result.ns       = information.getUint8(5);
      result.bf       = information.getUint8(6);
      result.wordId   = information.getUint8(7);
      result.length   = information.getUint8(8);
      let array       = information.buffer.slice(9, 9 + (result.length - 1));           
      let decoder     = new TextDecoder("utf-8"); 
      let uint8Array  = new Uint8Array(array);
      result.text = decoder.decode(uint8Array); 
      console.log(result.text);
      tmpWordInfo.current = [...tmpWordInfo.current, result];

      if (result.status === isInProgress) {
        return readVoiceRecognitionInformation();
      }
      else if (result.status === isFinished) {
        setWordInfo([...tmpWordInfo.current]);       
        return Promise.resolve();
      }
      else {
        // nothing
      }
    })
  }

  const handlePageChanged = (text) => {
    setCurrentPage(text);
  }

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider />
      <List>
        <ListItem button key={isVoiceRecogResult} onClick={() => handlePageChanged(isVoiceRecogResult)}>
         <ListItemText primary={isVoiceRecogResult} />
        </ListItem>
        <ListItem button key={isVoiceRecogSettigs} onClick={handleNestedListClicked}>
        <ListItemText primary={isVoiceRecogSettigs} />
        {openNestedList ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openNestedList} timeout="auto" unmountOnExit>
          <List component="div" disablePadding >
            <ListItem 
            button 
            className={classes.nested} 
            key={isCfThreshold} 
            onClick={() => handlePageChanged(isCfThreshold)}>
              <ListItemText primary={isCfThreshold} />
            </ListItem>
            <ListItem 
            button 
            className={classes.nested} 
            key={isVdThreshold} 
            onClick={() => handlePageChanged(isVdThreshold)}>
              <ListItemText primary={isVdThreshold} />
            </ListItem>
            <ListItem 
            button 
            className={classes.nested} 
            key={isGain} 
            onClick={() => handlePageChanged(isGain)}>
              <ListItemText primary={isGain} />
            </ListItem>
            <ListItem 
            button 
            className={classes.nested} 
            key={isNoiseSuppressor} 
            onClick={() => handlePageChanged(isNoiseSuppressor)}>
              <ListItemText primary={isNoiseSuppressor} />
            </ListItem>
            <ListItem 
            button 
            className={classes.nested} 
            key={isBeamForming} 
            onClick={() => handlePageChanged( isBeamForming)}>
              <ListItemText primary={isBeamForming} />
            </ListItem>
          </List>
        </Collapse>
        <ListItem button key={isIndoorEnvironment} onClick={() => handlePageChanged(isIndoorEnvironment)}>
         <ListItemText primary={isIndoorEnvironment} />
        </ListItem>
        <ListItem>
          <ListItemText key={isLedControl} primary={isLedControl} />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              onChange={handleSwitchcheckedToggle(isLedControl)}
              checked={switchChecked.indexOf(isLedControl) !== -1}
              inputProps={{ 'aria-labelledby': isLedControl }}
              color="primary" 
            />
          </ListItemSecondaryAction>
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button key={isInstructions} onClick={() => handlePageChanged(isInstructions)}>
          <ListItemText primary={isInstructions} />
        </ListItem>
      </List>
    </div>
  );


  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
            style={{color:"white"}}>
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
            RX23W HMI DEMO
          </Typography>
          <Button 
          color="inherit" 
          style={{color:"white"}} 
          disabled = {Boolean(bleButtonClicked)}
          onClick={handleBleToggle}>
            {connectionText}
          </Button>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <DisplayPage 
        currentPage={currentPage} 
        wordInfo={wordInfo}
        voiceRecogResult={voiceRecogResult}
        sensorData={sensorData}
        />
      </main>
      <Snackbar
        open={snackbarState.open}
        onClose={handleSnackbarClose}
        message={snackbarState.message}
        key={snackbarState.vertical + snackbarState.horizontal}
        autoHideDuration={snackbarState.autoHideDuration}
      />
      <Backdrop className={classes.backdrop} open={openCircle} >
        <CircularProgress color="inherit" />
      </Backdrop>
    </div>
  );
}

function DisplayPage(props) {
  const currentPage = props.currentPage;
  const wordInfo = props.wordInfo;
  const voiceRecogResult = props.voiceRecogResult;
  const sensorData = props.sensorData;

  switch (currentPage) {
    case isVoiceRecogResult :
      return (
        <PageIsVoiceRecogResult wordInfo={wordInfo} voiceRecogResult={voiceRecogResult}/>
      // <div>
      //   <Typography>Voice Recognition Result</Typography>
      //   <Typography>Word ID : {voiceRecogResult.wordId}</Typography>
      //   <Typography>Status : {voiceRecogResult.status}</Typography>
      //   <Typography>Score : {voiceRecogResult.score}</Typography>
      //   <DrawWordList wordInfo={wordInfo}/>
      // </div>
      );

    case isCfThreshold : 
      return (
        <div>
          <Typography>Confidence Threshold</Typography>
          {/* <PageIsVoiceRecogSettings wordInfo={wordInfo} parameter={isCfThreshold}/> */}
        </div>
      );

    case isVdThreshold : 
      return (
        <div>
          <Typography>Vocie Detection Threshold</Typography>
          {/* <PageIsVoiceRecogSettings wordInfo={wordInfo} parameter={isVdThreshold}/> */}
        </div>
      );   
      
    case isGain : 
      return (
        <div>
          <Typography>Gain</Typography>
          {/* <PageIsVoiceRecogSettings wordInfo={wordInfo} parameter={isGain}/> */}
        </div>
      ); 

    case isNoiseSuppressor : 
      return (
        <div>
          <Typography>Noise Suppressor</Typography>
          {/* <PageIsVoiceRecogSettings wordInfo={wordInfo} parameter={isNoiseSuppressor}/> */}
        </div>
      );   
      
    case isBeamForming : 
      return (
        <div>
          <Typography>Beam Forming</Typography>
          {/* <PageIsVoiceRecogSettings wordInfo={wordInfo} parameter={isBeamForming}/> */}
        </div>
      ); 

    case isIndoorEnvironment :
      return(
        <div>
          <PageIsIndoorEnvironment sensorData={sensorData} />
          {/* <Typography>Indoor Environment</Typography> */}
        {/* <Typography>Temperature : {sensorData.temperature} [â]</Typography>
        <Typography>Humidity : {sensorData.humidity} [%RH]</Typography>
        <Typography>CO2 : {sensorData.co2} [ppm]</Typography>
        <Typography>TVOC : {sensorData.tvoc} [mg/m3]</Typography>
        <Typography>IAQ : {sensorData.iaq}</Typography> */}
        </div>
      );

    case isInstructions :
      return (
        <div>
          {/* <PageIsInstructions /> */}
          <Typography>Instructions</Typography>
        </div>
        );

    default :
    return (
      <div>
        <Typography>Error</Typography>
      </div>
      );
  }
}

